import dayjs from 'dayjs';
import calendar from 'dayjs/plugin/calendar';
import durationPlugin from 'dayjs/plugin/duration';
import relativeTime from 'dayjs/plugin/relativeTime';
import type { Profile } from './indexing';

dayjs.extend(relativeTime);
dayjs.extend(calendar);
dayjs.extend(durationPlugin);

type Time =
  | 'yesterday'
  | `${number} months ago`
  | `1 month ago`
  | `${number} days ago`
  | `1 day ago`
  | `${number} hours ago`
  | `1 hour ago`
  | `${number} minutes ago`
  | `1 minute ago`
  | `${number} seconds ago`
  | `1 second ago`
  | 'tomorrow'
  | `${number} months from now`
  | `1 month from now`
  | `${number} days from now`
  | `1 day from now`
  | `${number} hours from now`
  | `1 hour from now`
  | `${number} minutes from now`
  | `1 minute from now`
  | `${number} seconds from now`
  | `1 second from now`
  | string
  | number
  | Date
  | dayjs.Dayjs
  | null
  | undefined;

const parseTime = (time: Time) => {
  if (time === 'yesterday') {
    return dayjs().subtract(1, 'day');
  }

  if (time === 'tomorrow') {
    return dayjs().add(1, 'day');
  }

  if (typeof time === 'string') {
    if (time.endsWith(' ago')) {
      const [number, unit] = time.split(' ');
      return dayjs().subtract(parseInt(number, 10), unit);
    }

    if (time.endsWith(' from now')) {
      const [number, unit] = time.split(' ');
      return dayjs().add(parseInt(number, 10), unit);
    }

    return dayjs(time);
  }

  return dayjs(time);
};

export type SessionStart = {
  pretty: (template?: string | undefined) => string;
  fromNow: (withoutSuffix?: boolean | undefined) => string;
  calendar: (
    referenceTime?: string | number | Date | dayjs.Dayjs | null | undefined,
    formats?: object | undefined
  ) => string;
  today: () => boolean;
  isBefore: (input: Time) => boolean;
  isAfter: (input: Time) => boolean;
};

export function start(profile: Pick<Profile, 'sessionStart'>): SessionStart {
  const djs = dayjs(profile.sessionStart);

  djs.calendar(null, {
    sameDay: '[Today at] h:mm A', // The same day ( Today at 2:30 AM )
    nextDay: '[Tomorrow]', // The next day ( Tomorrow at 2:30 AM )
    nextWeek: 'dddd', // The next week ( Sunday at 2:30 AM )
    lastDay: '[Yesterday]', // The day before ( Yesterday at 2:30 AM )
    lastWeek: '[Last] dddd', // Last week ( Last Monday at 2:30 AM )
    sameElse: 'DD/MM/YYYY', // Everything else ( 7/10/2011 )
  });

  return {
    pretty: djs.format.bind(djs),
    fromNow: djs.fromNow.bind(djs),
    calendar: djs.calendar.bind(djs),
    today: () => djs.isSame(dayjs(), 'day'),

    isBefore: (input: Time) => {
      const time = parseTime(input);
      return djs.isBefore(time);
    },
    isAfter: (input: Time) => {
      const time = parseTime(input);
      return djs.isAfter(time);
    },
  };
}

export type SessionDuration = {
  pretty: (withSuffix?: boolean | undefined) => string;
  raw: durationPlugin.Duration;
  seconds: () => number;
  minutes: () => number;
  hours: () => number;
  days: () => number;
  weeks: () => number;
  months: () => number;
};

export function duration(
  profile: Pick<Profile, 'sessionStart'>
): SessionDuration {
  const startTime = dayjs(profile.sessionStart);
  const duration = dayjs.duration(dayjs(Date.now()).diff(startTime));

  return {
    pretty: duration.humanize.bind(duration),
    raw: duration,
    seconds: () => Math.floor(duration.asSeconds()),
    minutes: () => Math.floor(duration.asMinutes()),
    hours: () => Math.floor(duration.asHours()),
    days: () => Math.floor(duration.asDays()),
    weeks: () => Math.floor(duration.asWeeks()),
    months: () => Math.floor(duration.asMonths()),
  };
}

export class Session {
  start: SessionStart;
  duration: SessionDuration;

  constructor(profile: Profile) {
    this.start = start(profile);
    this.duration = duration(profile);
  }
}
