export type JSONObject = { [key: string]: any };
export type JSONArray = Array<JSONObject>;
export type JSONValue =
  | string
  | number
  | boolean
  | null
  | JSONObject
  | JSONArray;

export interface AnalyticsEvent {
  type: 'track';
  event: string;
  properties?: JSONValue;
  received_at?: Date;
}

export interface PageDefault {
  path: string;
  referrer?: string;
  search?: string;
  title?: string;
  url?: string;
  utm?: Record<string, string>;
}

export interface PageView {
  page: PageDefault;
  visit_start?: Date;
  visit_end?: Date;
}

export interface TrafficSource {
  type: 'utm' | 'referrer';
  value: string;
}

export interface Raw {
  events?: AnalyticsEvent[];
  traits?: Record<string, JSONValue>;
  firmographics?: Record<string, JSONValue>;
  person?: Record<string, JSONValue>;
  pageViews?: PageView[];
  trafficSources?: TrafficSource[];
  sessionStart?: Date;
}

import {
  CountMinSketch,
  CountMinSketchOptions,
  JsBloom,
  JsBloomOptions,
} from '@koala-live/js-bloom';

export * from '@koala-live/js-bloom';

import type { Indexable, Profile } from './indexing';
import { Events } from './events';
import { Page } from './page';
import { Session } from './session';
import { Traits } from './traits';
import { inSegment, when } from './in-segment';
import { Company } from './company';
import { Person } from './person';

export type BuildOptions = {
  b?: JsBloomOptions;
  c?: CountMinSketchOptions;
  sessionStart?: Date;
};

import { index as rawIndex } from './indexing';

interface EdgeAPI {
  traits: Traits;
  events: Events;
  page: Page;
  session: Session;
  company: Company;
  person: Person;
  inSegment: typeof inSegment;
  when: typeof when;
  raw: Profile;
  index: (indexable: Indexable | Indexable[]) => void;
}

function index(profile: Profile, indexable: Indexable | Indexable[]): void {
  rawIndex(profile, indexable);
}

export function build(data: BuildOptions): EdgeAPI {
  const bloom = new JsBloom(data.b);
  const counts = new CountMinSketch(data.c);

  const profile: Profile = {
    counts,
    bloom,
    sessionStart: data.sessionStart ?? new Date(),
  };

  return {
    traits: new Traits(profile),
    events: new Events(profile),
    page: new Page(profile),
    session: new Session(profile),
    company: new Company(profile),
    person: new Person(profile),
    inSegment,
    when,
    raw: profile,
    index: (indexable: Indexable | Indexable[]) => index(profile, indexable),
  };
}
